input,
textarea {
  background-color: inherit;
  padding-top: .5rem;
  padding-bottom: .5rem;
  font-size: 14px;
  font-family: 'Open Sans', "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-style: 300;
}
input:focus,
textarea:focus {
  outline: -webkit-focus-ring-color auto 0px;
}
button {
  -webkit-appearance: square-button;
  background-color: inherit;
  border: 1px solid inherit;
  font-size: 14px;
  font-family: 'Open Sans', "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-style: 300;
  padding: .5rem;
}
button:hover {
  cursor: pointer;
}
h1 {
  margin-bottom: 0px;
  font-weight: 300;
}
h2 {
  padding-top: 0px;
  margin-bottom: 0px;
  margin-top: 0px;
  font-weight: 300;
}
h4 {
  font-weight: 300;
  margin-top: 0px;
}
hr {
  margin-top: 0px;
}
.glass-button {
  border: 1px solid gray;
}
.glass-card-footer {
  border: 1px solid inherit;
}
.glass-footer-btn {
  border: 0px solid white;
}
.glass-footer-btn:focus {
  outline: none;
}
.glass-footer-btn:hover {
  border: 0px solid white;
  color: maroon;
}
#mainPanel:hover {
  cursor: pointer;
}
#mainPanel:hover .indexButton,
#mainPanel:hover .imageGridButton,
#mainPanel:hover .listButton,
#mainPanel:hover .lockButton,
#mainPanel:hover .tableButton,
#mainPanel:hover .pageFooter {
  visibility: visible;
}
#mainPanel:hover .indexButton:hover,
#mainPanel:hover .listButton:hover,
#mainPanel:hover .imageGridButton:hover,
#mainPanel:hover .recordId:hover,
#mainPanel:hover .lockButton:hover,
#mainPanel:hover .tableButton:hover,
#mainPanel:hover .pageFooter:hover {
  color: maroon;
}
.page .indexButton {
  position: absolute;
  top: 10px;
  left: 10px;
  font-size: 150%;
  visibility: hidden;
}
.page .imageGridButton {
  position: absolute;
  top: 10px;
  right: 50px;
  font-size: 150%;
  visibility: hidden;
}
.page .listButton {
  position: absolute;
  top: 10px;
  right: 90px;
  font-size: 150%;
  visibility: hidden;
}
.page .tableButton {
  position: absolute;
  top: 10px;
  right: 130px;
  font-size: 150%;
  visibility: hidden;
}
.page .lockButton,
.page .unlockButton {
  position: absolute;
  top: 10px;
  left: 355px;
  font-size: 150%;
  visibility: hidden;
}
.page .pageFooter {
  position: relative;
  width: 100%;
  border-top: 1px solid gray;
}
.fullwidth {
  width: 100%;
}
.hidden {
  visibility: hidden;
}
.form-control {
  width: 100%;
  margin-bottom: 10px;
  border-top: 1px solid gray !important;
  border-bottom: 0px;
  border-left: 0px;
  border-right: 0px;
}
.recordId {
  position: absolute;
  top: 10px;
  font-size: 200%;
  letter-spacing: 3px;
}
.glass {
  border: 0px;
  width: 100%;
}
