/* CSS declarations go here */
body {
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  font-size: .8em;
  -webkit-text-size-adjust: auto;
  background-color: black;
}
#transparentPanel {
  position: absolute;
  top: 100px;
  min-height: 60px;
  width: 100%;
  opacity: .8;
  left: 0;
  background-color: white;
  border-corner-shape: bevel;
  border-radius: 2px;
  background: linear-gradient(225deg, transparent 28.28px, #ffffff 0) top right;
}
#mainPanel {
  position: absolute;
  top: 100px;
  left: 0px;
  min-height: 200px;
  background-color: inherit;
  width: 100%;
}
#mainPanel #innerPanel {
  background-color: white;
  margin: 20px;
  padding: 20px;
}
.hidden {
  visibility: hidden;
}
h1 {
  margin-bottom: 0px;
  font-weight: 300;
}
h2 {
  padding-top: 20px;
  margin-bottom: 0px;
  font-weight: 300;
}
h4 {
  color: gray;
  font-weight: 300;
  margin-top: 0px;
}
hr {
  margin-top: 0px;
}
p {
  line-height: 140%;
}
a {
  text-decoration: none;
  color: steelblue;
}
.code {
  background-color: #eeeeee;
  padding: 10px;
}
.gray {
  color: #999999;
}
.lightgray {
  color: #dddddd;
}
.fa-github {
  cursor: pointer;
}
.large {
  font-size: 2em !important;
}
#cookbookLink {
  left: 50%;
}
@media only screen and (min-width: 768px) {
  #transparentPanel {
    max-width: 768px;
  }
  #mainPanel {
    max-width: 768px;
  }
}
